.homepageWrapper {
  height: 500vh;
  margin-left: 25%;
  width: 75%;

}

.section {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: 'Open Sans', sans-serif;
  color: #D1DECE;
  font-size: 2.5vh;
  min-height: 100vh;
  width: 100%;
  /* border: 1px solid black; */
}

.sectionLabel {
  font-family: 'Lato', sans-serif;
  color: white;
  font-size: 5vh;
  font-weight: 200;
  text-align: center;
  padding-top: 8vh;
  padding-bottom: 3vh;
  border-bottom: 2px solid seagreen;
}

